@charset "UTF-8";
/**
 *  
 * ,  size-2x, space-2x, size-base ...
 * , s2, s1
 * : `$s1 - $s8`
 * @example scss - 
 *   .element {
 *     padding: $s1 !default;
 *   }
 *
 * @example css - CSS 
 *   .element {
 *     padding: 4px !default;
 *   }
 */
.next-overlay-wrapper .next-overlay-inner {
  z-index: 1001; }

.next-overlay-wrapper .next-overlay-backdrop {
  position: fixed;
  z-index: 1001;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #000000;
  transition: opacity .3s;
  opacity: 0; }

.next-overlay-wrapper.opened .next-overlay-backdrop {
  opacity: 0.2; }
